export default class AppModel {
  constructor(state) {
    this.state = state;
  }

  static extractClipNames(data) {
    return data.items.map(clip => clip.snippet.title);
  }

  async getClipNames() {
    const { url } = this.state;
    const responce = await fetch(url);
    const data = await responce.json();
    return AppModel.extractClipNames(data);
  }

  static extractClipImages(data) {
    return data.items.map(clip => clip.snippet.thumbnails.medium.url);
  }

  async getClipImages() {
    const { url } = this.state;
    const responce = await fetch(url);
    const data = await responce.json();
    return AppModel.extractClipImages(data);
  }

  static extractChannelName(data) {
    return data.items.map(clip => clip.snippet.channelTitle);
  }

  async getChannelName() {
    const { url } = this.state;
    const responce = await fetch(url);
    const data = await responce.json();
    return AppModel.extractChannelName(data);
  }

  static extractChannelDescription(data) {
    return data.items.map(clip => clip.snippet.description);
  }

  async getChannelDescription() {
    const { url } = this.state;
    const responce = await fetch(url);
    const data = await responce.json();
    return AppModel.extractChannelDescription(data);
  }

  static extractChannelDate(data) {
    return data.items.map(clip => clip.snippet.publishedAt);
  }

  async getChannelDate() {
    const { url } = this.state;
    const responce = await fetch(url);
    const data = await responce.json();
    return AppModel.extractChannelDate(data);
  }

  static extractChannelId(data) {
    return data.items.map(clip => clip.id.videoId);
  }

  async getChannelId() {
    const { url } = this.state;
    const responce = await fetch(url);
    const data = await responce.json();
    return AppModel.extractChannelId(data);
  }

  static extractChannelView(data) {
    return data.items.map(clip => clip.statistics.viewCount);
  }

  async getChannelView() {
    const { url } = this.state;
    const responce = await fetch(url);
    const data = await responce.json();
    return AppModel.extractChannelView(data);
  }

  async getNextPageToken() {
    const { url } = this.state;
    const responce = await fetch(url);
    const data = await responce.json();
    return AppModel.extractNextPageToken(data);
  }

  static extractNextPageToken(data) {
    return data.nextPageToken;
  }
}
