export default class AppView {
  constructor(titles, images, names, descriptions, dates, views, id, maxRes) {
    this.titles = titles;
    this.images = images;
    this.names = names;
    this.descriptions = descriptions;
    this.dates = dates;
    this.views = views;
    this.id = id;
    this.maxRes = maxRes;
  }

  static metaTag() {
    const head = document.querySelector('head');
    const meta = document.createElement('meta');

    meta.name = 'viewport';
    meta.content = 'width=device-width, initial-scale=1';
    head.appendChild(meta);
  }

  static createInput() {
    const input = document.createElement('input');
    input.type = 'text';
    input.id = 'input';
    document.body.appendChild(input);
  }

  static deleteContainer() {
    document.getElementById('container').remove();
  }

  static deleteButton() {
    document.getElementById('buttonField').remove();
  }

  static createContainers() {
    const container = document.createElement('div');
    container.id = 'container';
    document.body.appendChild(container);

    const content = document.createElement('div');
    content.className = 'content';
    content.id = 'content';
    container.appendChild(content);
  }

  static buttons() {
    const buttonField = document.createElement('div');
    buttonField.className = 'buttonField';
    document.body.appendChild(buttonField);

    const leftButton = document.createElement('button');
    leftButton.className = 'leftButton';
    buttonField.appendChild(leftButton);

    const rightButton = document.createElement('button');
    rightButton.className = 'rightButton';
    buttonField.appendChild(rightButton);
  }

  render() {
    for (let i = 0; i < this.maxRes; i += 1) {
      const clips = document.createElement('div');
      clips.className = 'clips';
      clips.id = 'clips';
      document.getElementById('content').appendChild(clips);


      const titleBlock = document.createElement('div');
      titleBlock.className = 'titleBlock';
      clips.appendChild(titleBlock);

      const titleURL = document.createElement('a');
      titleURL.className = 'title-url';
      titleURL.href = `https://www.youtube.com/watch?v=${this.id[i]}`;
      titleURL.target = '_blank';
      titleBlock.appendChild(titleURL);

      const title = document.createElement('h2');
      title.innerHTML = this.titles[i];
      title.className = 'title';
      titleURL.appendChild(title);

      const image = document.createElement('img');
      image.src = this.images[i];
      image.className = 'image';
      clips.appendChild(image);

      const nameImg = document.createElement('img');
      nameImg.src = '/./youtube-client/a/user-black-close-up-shape.png';
      nameImg.className = 'nameImg';
      clips.appendChild(nameImg);

      const name = document.createElement('p');
      name.innerHTML = this.names[i];
      name.className = 'name';
      clips.appendChild(name);

      const calImg = document.createElement('img');
      calImg.src = '/./youtube-client/a/calendar.png';
      calImg.className = 'calImg';
      clips.appendChild(calImg);

      const date = document.createElement('p');
      date.innerHTML = (this.dates[i].slice(0, 10));
      date.className = 'date';
      clips.appendChild(date);

      const eyeImg = document.createElement('img');
      eyeImg.src = '/./youtube-client/a/eye.png';
      eyeImg.className = 'eyeImg';
      clips.appendChild(eyeImg);

      const view = document.createElement('p');
      view.innerHTML = this.views[i];
      view.className = 'view';
      clips.appendChild(view);

      const description = document.createElement('p');
      description.innerHTML = this.descriptions[i];
      description.className = 'description';
      clips.appendChild(description);
    }
  }
}
