import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  constructor() {
    this.state = {
      url: 'https://www.googleapis.com/youtube/v3/',
    };
  }

  static getAPIKey() {
    return 'key=AIzaSyDa4NYaCB5iactEDbv1sNJ9PlHP22nk25E';
  }

  static maxRes() {
    return 15;
  }

  static inputReturn() {
    return document.getElementById('input').value;
  }

  static renderVideo(token) {
    const slider = document.querySelector('#container');
    let firstSlider = 1;
    let secondtSlider = 2;
    let thirdSlider = 3;
    let fourthSlider = 4;
    if (slider.offsetWidth === 1465) {
      if (Math.round((App.maxRes() - Math.round(slider.scrollLeft
        / slider.offsetWidth + 1)) / fourthSlider) <= 3) {
        const app = new App();
        app.nextToken(token);
        fourthSlider += fourthSlider;
      }
    }

    if (slider.offsetWidth === 1100) {
      if (Math.round((App.maxRes() - Math.round(slider.scrollLeft
        / slider.offsetWidth + 1)) / thirdSlider) <= 3) {
        const app = new App();
        app.nextToken(token);
        thirdSlider += thirdSlider;
      }
    }

    if (slider.offsetWidth === 735) {
      if (Math.round((App.maxRes() - Math.round(slider.scrollLeft
        / slider.offsetWidth + 1)) / secondtSlider) <= 4) {
        const app = new App();
        app.nextToken(token);
        secondtSlider += secondtSlider;
      }
    }
    if (slider.offsetWidth === 364) {
      if (Math.round((App.maxRes() - Math.round(slider.scrollLeft
        / slider.offsetWidth + 1)) / firstSlider) <= 1) {
        const app = new App();
        app.nextToken(token);
        firstSlider += firstSlider;
      }
    }
  }

  static slider(token) {
    const slider = document.querySelector('#container');
    let isDown = false;
    let startX;


    slider.addEventListener('mousedown', (e) => {
      isDown = true;
      slider.classList.add('active');
      startX = e.pageX - slider.offsetLeft;
    });
    slider.addEventListener('mouseleave', () => {
      isDown = false;
      slider.classList.remove('active');
    });
    slider.addEventListener('mouseup', () => {
      isDown = false;
      slider.classList.remove('active');
      App.renderVideo(token);
    });
    slider.addEventListener('mousemove', (e) => {
      if (isDown) {
        e.preventDefault();
        const x = e.pageX - slider.offsetLeft;
        const walk = x - startX;
        if (walk < 0) {
          slider.scrollLeft += document.getElementById('container').offsetWidth;
        } else slider.scrollLeft -= document.getElementById('container').offsetWidth;
      }
    });
  }

  static sliderButton(token) {
    const buttonLeft = document.querySelector('.leftButton');
    const buttonRight = document.querySelector('.rightButton');
    const slider = document.querySelector('#container');


    buttonLeft.addEventListener('click', () => {
      slider.scrollLeft -= slider.offsetWidth;
    });

    buttonRight.addEventListener('click', () => {
      slider.scrollLeft += slider.offsetWidth;
      App.renderVideo(token);
    });
  }

  async start() {
    if (document.getElementById('container')) {
      AppView.deleteContainer();
      AppView.deleteButton();
    }

    let state = {
      url: (this.state.url).concat('search?', App.getAPIKey(), '&type=video&part=snippet&', `maxResults=${App.maxRes()}`, '&q=', App.inputReturn()),
    };

    let model = new AppModel(state);
    const dataTitle = await model.getClipNames();
    const dataImg = await model.getClipImages();
    const dataName = await model.getChannelName();
    const dataDescription = await model.getChannelDescription();
    const dataDate = await model.getChannelDate();
    const dataID = await model.getChannelId();
    const token = await model.getNextPageToken();
    state = {
      url: (this.state.url).concat('videos?', App.getAPIKey(), '&id=', dataID, '&part=snippet,statistics'),
    };
    model = new AppModel(state);
    const views = await model.getChannelView();
    const view = new AppView(dataTitle, dataImg, dataName, dataDescription,
      dataDate, views, dataID, App.maxRes());

    AppView.createContainers();
    view.render();
    App.slider(token);
    AppView.buttons();
    App.sliderButton(token);
  }

  async nextToken(token) {
    let state = {
      url: (this.state.url).concat('search?', App.getAPIKey(), '&type=video&part=snippet&', `maxResults=${App.maxRes()}`, '&q=', App.inputReturn(), `&pageToken=${token}`),
    };

    let model = new AppModel(state);
    const dataTitle = await model.getClipNames();
    const dataImg = await model.getClipImages();
    const dataName = await model.getChannelName();
    const dataDescription = await model.getChannelDescription();
    const dataDate = await model.getChannelDate();
    const dataID = await model.getChannelId();
    const tokenNext = await model.getNextPageToken();
    state = {
      url: (this.state.url).concat('videos?', App.getAPIKey(), '&id=', dataID, '&part=snippet,statistics'),
    };
    model = new AppModel(state);
    const views = await model.getChannelView();
    const view = new AppView(dataTitle, dataImg, dataName, dataDescription,
      dataDate, views, dataID, App.maxRes());

    view.render();
    App.slider(tokenNext);
  }
}
